<template>
    <div>
        <v-container>
            <v-row class="">
                <v-col class="d-flex justify-end px-0">
                    <v-btn @click="toProject">
                        <v-icon> mdi-chevron-left </v-icon>
                        back
                    </v-btn>
                </v-col>
            </v-row>
            <v-row class="white rounded">
                <v-col cols="12" md="6">
                    <span> 
                        {{ dataMail.name }} 
                    </span><br>
                    <span>
                        {{ dataMail.email }}
                    </span>
                </v-col>
                <v-col cols="12" md="6" class="d-flex justify-end">
                    <span>
                        {{ dataMail.created_at }}
                    </span>
                </v-col>
                <v-col cols="12">
                    <span>{{ dataMail.title }} </span>
                </v-col>
                <v-col>
                    <span> {{ dataMail.message }} </span>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "ReadMail",

    mounted() {
        this.setMailMessage(this.$route.params.slug);
    },

    computed: {
        ...mapGetters({
            dataMail: "getMailMessage",
        }),
    },

    methods: {
        ...mapActions({
            setMailMessage: "setMailMessage",
        }),
        toProject() {
            this.$router.push({ name: "Inbox" });
        },
    },

    metaInfo() {
        return {
            titleTemplate: "%s | " + this.dataMail.title,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.dataMail.title,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

