<template>
    <div id="dashboard-page">
        <v-container fluid class="pa-0">
            <v-row>
                <v-col
                    cols="12"
                    md="3"
                    v-for="(n, i) in dashboardData"
                    :key="i"
                >
                    <v-sheet height="100" rounded color="" class="d-flex pa-2 px-4">
                        <v-icon size="60" color="#5e72e4">
                            {{ n.icon }}
                        </v-icon>
                    </v-sheet>
                </v-col>
            </v-row>
        </v-container>
        
        <v-alert class="rounded-lg" colored-border type="info" elevation="2">
            Your email
            <span class="mx-2 font-weight-bold green--text">
                current user name
            </span>
            has not been verified, please verify your email before using this
            application.
            <v-btn class="ml-2" color="success">
                send email verification
            </v-btn>
        </v-alert>
        <v-alert class="rounded-lg" colored-border type="info" elevation="2">
            You have not completed your profile, please complete the profile to
            use this application.
            <v-btn class="ml-2" color="success">
                profile
            </v-btn>
        </v-alert>
        <div class="">
            <v-btn>current User</v-btn>
        </div>
    </div>
</template>

<script>
import { fb } from "@/components/firebaseInit";
import { mapGetters } from "vuex";
export default {
    name: "Dashboard",
    components: {
        //
    },
    data() {
        return {
            emailVerificationStatus: false,
            dashboardData: [
                {
                    name: "total visit",
                    icon: "mdi-account-group",
                    value: 20,
                },
                {
                    name: "today",
                    icon: "mdi-account-arrow-right",
                    value: 10,
                },
                {
                    name: "project",
                    icon: "mdi-star",
                    value: 5,
                },
                {
                    name: "mails",
                    icon: "mdi-email",
                    value: 20,
                },
            ],
        };
    },
    computed: {
        ...mapGetters({
            getCurrentUser: "getCurrentUser",
        }),
        currentRouteName() {
            return this.$route.meta.title;
        },
    },
    methods: {
        verifiedEmail() {
            const user = fb.auth().currentUser;
            user.sendEmailVerification()
                .then(() => {
                    console.log("send email verification");
                    alert(`Verification email has been sent to ${user.email}`);
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        checkCurrentUser() {
            const user = fb.auth().currentUser;
            console.log(user);
        },
    },

    metaInfo() {
        return {
            // title: 'tes',
            // override the parent template and just use the above title only
            titleTemplate: "%s | " + this.currentRouteName,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.currentRouteName,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

<style scoped>
#dashboard-page {
    height: 120vh;
}
</style>