<template>
    <div class="">
        <v-container fluid class="pa-0">
            <v-row>
                <v-col cols="12" md="8">
                    <v-btn
                        large
                        :outlined="sideBarStt == n.action ? true : false"
                        color="white"
                        v-for="(n, i) in sideBar"
                        :key="i"
                        class="mr-2"
                        @click="changeMail(n.action)"
                    >
                        <v-icon
                            left
                            :color="
                                sideBarStt == n.action ? 'white' : '#4252af'
                            "
                        >
                            {{ n.icon }}
                        </v-icon>
                        <span
                            :style="{
                                color:
                                    sideBarStt == n.action
                                        ? 'white'
                                        : '#4252af',
                            }"
                        >
                            {{ n.name }}
                        </span>
                    </v-btn>
                    <v-btn
                        large
                        :outlined="advanceSearch ? true : false"
                        color="white"
                        class="mr-2"
                        @click="advanceSearch = !advanceSearch"
                    >
                        <v-icon
                            left
                            :color="advanceSearch ? 'white' : '#4252af'"
                            :class="advanceSearch ? 'mdi-spin' : ''"
                        >
                            mdi-cog
                        </v-icon>
                        <span
                            :style="{
                                color: advanceSearch ? 'white' : '#4252af',
                            }"
                        >
                            option
                        </span>
                    </v-btn>
                </v-col>
                <v-col cols="12" md="4" class="d-flex align-center">
                    <v-text-field
                        clearable
                        label="Search . . ."
                        solo
                        hide-details="auto"
                        v-model="search"
                        append-icon="mdi-magnify"
                        @click:append="alert()"
                    ></v-text-field>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="4" v-show="advanceSearch">
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="dates"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                    >
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                                v-model="dates"
                                label="Range Date . . ."
                                readonly
                                solo
                                v-bind="attrs"
                                v-on="on"
                                clearable
                                hide-details="auto"
                                append-icon="mdi-magnify"
                                @click:append="alert()"
                            ></v-text-field>
                        </template>
                        <v-date-picker
                            v-model="dates"
                            no-title
                            scrollable
                            range
                        >
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu = false">
                                Cancel
                            </v-btn>
                            <v-btn
                                text
                                color="primary"
                                @click="$refs.menu.save(dates)"
                            >
                                OK
                            </v-btn>
                        </v-date-picker>
                    </v-menu>
                </v-col>
                <v-col cols="4" v-show="advanceSearch">
                    <v-btn-toggle
                        v-model="toggle_one"
                        mandatory
                        color="#5e72e4"
                    >
                        <v-btn>
                            <span class="font-weight-bold">lastest</span>
                        </v-btn>

                        <v-btn>
                            <span class="font-weight-bold">newest</span>
                        </v-btn>
                    </v-btn-toggle>
                </v-col>
                <LoadingDialog />
                <v-col cols="12">
                    <ListMail />
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import { db } from "@/components/firebaseInit";
import { mapActions, mapGetters } from "vuex";
export default {
    name: "Inbox",

    components: {
        LoadingDialog: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/CoreComponent/LoadingDialog.vue"
            ),
        ListMail: () =>
            import(/* webpackChunkName: "navbar" */ "./ListMail.vue"),
    },

    async created() {
        await this.fetchData();
    },

    data: () => ({
        search: "",
        advanceSearch: false,
        dates: [],
        menu: false,
        toggle_one: 0,
    }),

    computed: {
        ...mapGetters({
            sideBar: "getSideBar",
            sideBarStt: "getSideBarStt",
        }),
        dateRangeText() {
            return this.dates.join(" ~ ");
        },
        currentRouteName() {
            return this.$route.meta.title;
        },
    },

    methods: {
        ...mapActions({
            setAllMail: "setAllMail",
            changeMail: "changeMail",
            setLoading: "updateLoadingDialogStatus"
        }),
        async fetchData() {
            db.collection("inbox_mail")
                .orderBy("created_at")
                .get()
                .then((querySnapshot) => {
                    // this.loading = false;
                    let data = [];
                    querySnapshot.forEach((doc) => {
                        data.push({
                            ...doc.data(),
                            id: doc.id,
                        });
                    });
                    this.setAllMail(data);
                    this.changeMail("all");
                    this.setLoading(false)
                })
                .catch((e) => {
                    console.log(e); // "oh, no!"
                });
        },
        alert() {
            alert("tes");
        },
    },

    beforeDestroy () {
        this.setLoading(true);
    },

    metaInfo() {
        return {
            // title: 'tes',
            // override the parent template and just use the above title only
            titleTemplate: "%s | " + this.currentRouteName,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.currentRouteName,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

<style scoped>
.a {
    color: #4252af;
}
</style>