<template>
    <div>
        <v-container>
        </v-container>
    </div>
</template>

<script>
export default {
    name: "Notif",

    computed: {
        currentRouteName() {
            return this.$route.meta.title;
        },
    },

    metaInfo() {
        return {
            // title: 'tes',
            // override the parent template and just use the above title only
            titleTemplate: "%s | " + this.currentRouteName,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.currentRouteName,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

